<template>
  <GmapMap
    :center="center"
    :zoom="7"
    map-type-id="terrain"
    style="width: 500px; height: 300px"
  >
    <GmapMarker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.position"
      :clickable="true"
      :draggable="true"
      @click="center = m.position"
    />
  </GmapMap>
</template>

<script>
export default {
  data() {
    return {
      markers: [
        { position: { lat: 31.547201, lng: 34.832581 } },
        { position: { lat: 32.047104, lng: 34.832384 } },
        { position: { lat: 32.747098, lng: 34.972021 } },
      ],
      center: { lat: 32.047175, lng: 34.832301 },
    };
  },
  created() {
    navigator.geolocation.getCurrentPosition((position) => {
      const { latitude, longitude } = position.coords;
      this.center.lat = latitude;
      this.center.lng = longitude;
    });
  },
};
</script>

