<template>
  <section class="app-main">
    <toy-filter @filtered="setFilter" @sorted="setSort" main-layout />
    <div class="add-toy-section">
      <router-link
        v-if="getIsLoggedinUserAdmin"
        class="router-link-btn"
        to="/toy/edit"
        >Add toy</router-link
      >
    </div>

    <p v-if="isLoading">Loading...</p>
    <toy-list v-else-if="toys" :toys="toys" @remove="removeToy" />
  </section>
</template>

<script>
import { showMsg } from "@/services/event-bus-service.js";
import toyFilter from "@/cmps/toy-filter.vue";
import toyList from "@/cmps/toy-list.vue";

export default {
  name: "toyApp",
  components: {
    toyFilter,
    toyList,
  },
  data() {
    return {};
  },
  computed: {
    toys() {
      return this.$store.getters.toysToShow;
    },

    isLoading() {
      return this.$store.getters.isLoading;
    },
    getIsLoggedinUserAdmin() {
      return this.$store.getters.getIsLoggedinUserAdmin;
    },
    // pages() {
    //   return this.$store.getters.getNumOfPages;
    // },
  },
  created() {
    this.$store.dispatch({ type: "loadToys" });
  },
  methods: {
    async setFilter(filterBy) {
      try {
        const copyFilter = JSON.parse(JSON.stringify(filterBy));
        await this.$store.commit({ type: "setFilter", copyFilter });
        await this.$store.dispatch({ type: "loadToys" });
      } catch (err) {
        console.log(err);
      }
    },
    setSort(sortBy) {
      const copySort = JSON.parse(JSON.stringify(sortBy));
      this.$store.commit({ type: "setSort", copySort });
    },
    async removeToy(toyId) {
      try {
        const deletedId = await this.$store.dispatch({
          type: "removeToy",
          toyId,
        });
        if (deletedId) showMsg(`Toy removed ${deletedId}`);
        else showMsg(`Yoy are not allowed to remove`, "danger");
      } catch (err) {
        console.log(err);
        showMsg(`Failed to remove Toy`);
      }
    },
  },
};
</script>
