<template>
  <div class="">
    <section v-if="isLoading">
      <p>Loading...</p>
    </section>
    <section v-else class="toy-edit-container app-main">
      <h3 v-if="toyToEdit">{{ title }}</h3>
      <form v-if="toyToEdit" @submit.prevent="save" class="toy-edit-form">
        <label
          >Toy name:
          <input type="text" placeholder="name" v-model="toyToEdit.name" />
        </label>

        <ValidationProvider rules="positive" v-slot="{ errors }">
          <label
            >Toy price:
            <input
              type="number"
              placeholder="price"
              v-model="toyToEdit.price"
            />
            <span>{{ errors[0] }}</span>
          </label>
        </ValidationProvider>
        <label
          >Is in stock?
          <select v-model="toyToEdit.inStock">
            <option :value="true">Yes</option>
            <option :value="false">No</option>
          </select>
        </label>
        <label>Choose labels:</label>
        <select v-if="toyToEdit.label" v-model="toyToEdit.label" multiple>
          <option value="On wheels">On wheels</option>
          <option value="Box game">Box game</option>
          <option value="Art">Art</option>
          <option value="Baby">Baby</option>
          <option value="Doll">Doll</option>
          <option value="Puzzle">Puzzle</option>
          <option value="Outdoor">Outdoor</option>
        </select>
        <button class="btn-action btn-save">Save</button>
      </form>
      <router-link class="router-link-btn" to="/toy">Back</router-link>
    </section>
  </div>
</template>

<script>
// import { showMsg } from "@/services/event-bus.service.js";
import { ValidationProvider } from "vee-validate";
import { extend } from "vee-validate";
extend("positive", (value) => {
  return value >= 0;
});

export default {
  name: "toyEdit",
  components: {
    ValidationProvider,
  },
  data() {
    return {
      toyToEdit: {},
      priceValidation: 0,
    };
  },
  created() {
    if (this.toyId) {
      this.$store
        .dispatch({ type: "setCurrToy", toyId: this.toyId })
        .then(() => {
          this.toyToEdit = this.$store.getters.getCurrToy;
          console.log("this.toyToEdit", this.toyToEdit);
        });
    } else {
      this.$store
        .dispatch({ type: "setNewToy" })
        .then(() => (this.toyToEdit = this.$store.getters.getCurrToy));
    }
  },

  methods: {
    save() {
      console.log("toy label", this.toyToEdit.label);
      this.$store
        .dispatch({ type: "saveToy", toy: this.toyToEdit })
        .then((toy) => {
          //   showMsg(`updated/add new toy with id ${toy._id}`);
          console.log("save new");
          this.$router.push("/toy");
        });
    },
  },
  computed: {
    getCurrentToy() {
      return this.$store.getters.getCurrToy;
    },
    title() {
      return this.toyId ? "Edit toy" : "Add toy";
    },
    toyId() {
      console.log(this.$route.params.toyId);
      return this.$route.params.toyId || "";
    },
    isLoading() {
      return this.$store.getters.isLoading;
    },
  },
};
</script>
