import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../pages/home.vue'
import about from '../pages/about.vue'
import signup from '../pages/user-signup.vue'
import login from '../pages/user-login.vue'
import toyApp from '../pages/toy-app.vue'
import toyEdit from '../pages/toy-edit.vue'
import toyDetails from '../pages/toy-details.vue'
import dashboard from '../pages/dashboard.vue'
import reviewApp from '../pages/review-app.vue'
import userDetails from '../pages/user-details.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home,
  },
  {
    path: '/about',
    name: 'about',
    component: about,
  },
  {
    path: '/signup',
    name: 'signup',
    component: signup,
  },
  {
    path: '/review',
    name: 'review',
    component: reviewApp,
  },
  {
    path: '/login',
    name: 'login',
    component: login,
  },
  {
    path: '/user/:id',
    name: 'user-details',
    component: userDetails,
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: dashboard,
  },
  {
    path: '/toy',
    name: 'toyApp',
    component: toyApp,
  },
  {
    path: '/toy/edit/:toyId?',
    name: 'toyEdit',
    component: toyEdit,
  },
  {
    path: '/toy/:toyId',
    name: 'toyDetails',
    component: toyDetails,
  },
]

const router = new VueRouter({
  routes,
})

export default router
