import { storageService } from './async-storage-service.js'
import { httpService } from './http.service'
// import { socketService, SOCKET_EVENT_USER_UPDATED } from './socket.service'
const STORAGE_KEY_LOGGEDIN_USER = 'loggedinUser'
var gWatchedUser = null

const STORAGE_KEY = 'user'

const KEY = 'toysDB'
const BASE_URL =
  process.env.NODE_ENV !== 'development'
    ? '/api/auth/'
    : '//localhost:3000/api/auth/'

const BASE_URL2 =
  process.env.NODE_ENV !== 'development'
    ? '/api/user/'
    : '//localhost:3000/api/user/'

export const userService = {
  login,
  logout,
  signup,
  getLoggedinUser,
  getUsers,
  getById,
  remove,
  update,
  changeScore,
}

// Debug technique
window.userService = userService

async function login(username, password) {
  try {
    const res = await httpService.post(
      // BASE_URL + 'login',
      'auth/login',
      {
        username,
        password,
      }
      // { withCredentials: true }
    )
    console.log('res', res)
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(res.data))
    return _saveLocalUser(res)
  } catch (err) {
    console.log(err)
  }
}
async function signup(username, password, fullname) {
  try {
    const res = await httpService.post(
      // BASE_URL + 'signup',
      'auth/signup',
      {
        username,
        password,
        fullname,
      }
      // { withCredentials: true }
    )
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(res.data))
    return _saveLocalUser(res)
  } catch (err) {
    console.log(err)
  }
  // return storageService.post(STORAGE_KEY, userInfo).then((user) => {
  //   sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(user))
  //   return user
  // })
}
async function logout() {
  try {
    const res = await httpService.post(
      // BASE_URL + '/logout',
      'auth/logout',
      null
      //  {
      //   withCredentials: true,
      // }
    )
    sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, null)
    return res.data
  } catch (err) {
    console.log(err)
  }
}

function getLoggedinUser() {
  return JSON.parse(sessionStorage.getItem(STORAGE_KEY_LOGGEDIN_USER)) || null
}

function _saveLocalUser(user) {
  sessionStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(user))
  return user
}

async function changeScore(by) {
  const user = getLoggedinUser()
  if (!user) throw new Error('Not loggedin')
  user.score = user.score + by || by
  await update(user)
  return user.score
}

function getUsers() {
  // return storageService.query('user')
  return httpService.get(`user`)
}

async function getById(userId) {
  // const user = await storageService.get('user', userId)
  const user = await httpService.get(`user/${userId}`)
  gWatchedUser = user
  return user
}
function remove(userId) {
  // return storageService.remove('user', userId)
  return httpService.delete(`user/${userId}`)
}

async function update(user) {
  // await storageService.put('user', user)
  user = await httpService.put(`user/${user._id}`, user)
  // Handle case in which admin updates other user's details
  if (getLoggedinUser()._id === user._id) _saveLocalUser(user)
  return user
}

