const KEY = 'toysDB'
const BASE_URL =
  process.env.NODE_ENV !== 'development'
    ? '/api/toy/'
    : '//localhost:3000/api/toy/'

const axios = require('axios')

export const toyService = {
  query,
  getById,
  remove,
  save,
  getEmptyToy,
}

// var gToys = _createToys()

async function query(filterBy) {
  // return storageService.query(KEY)
  try {
    const res = await axios.get(BASE_URL, {
      params: filterBy,
    })
    return res.data
  } catch (err) {
    console.log(err)
  }
  // return axios
  //   .get(BASE_URL, {
  //     params: filterBy,
  //   })
  //   .then((res) => res.data)
}

async function getById(id) {
  // return storageService.get(KEY, id)
  // return axios.get(BASE_URL + id).then((res) => res.data)
  try {
    const res = await axios.get(BASE_URL + id)
    return res.data
  } catch (err) {
    console.log(err)
  }
}

async function remove(id) {
  // return storageService.remove(KEY, id)
  // return axios.delete(BASE_URL + id).then((res) => res.data)
  try {
    console.log('remove get this id', id)
    const res = await axios.delete(BASE_URL + id, {
      withCredentials: true,
    })
    console.log('res.data', res.data)
    return res.data
  } catch (err) {
    console.log(err)
  }
}

async function save(toy) {
  // const savedToy = toy._id
  //   ? storageService.put(KEY, toy)
  //   : storageService.post(KEY, toy)
  // return savedToy
  if (toy._id) {
    // return axios.put(BASE_URL + toy._id, toy).then((res) => res.data)
    try {
      const res = await axios.put(BASE_URL + toy._id, toy, {
        withCredentials: true,
      })
      return res.data
    } catch (err) {
      console.log(err)
    }
  } else {
    // return axios.post(BASE_URL, toy).then((res) => res.data)
    try {
      const res = await axios.post(BASE_URL, toy, { withCredentials: true })
      return res.data
    } catch (err) {
      console.log(err)
    }
  }
}

