<template>
  <div class="dashboard-section">
    <h1>Dashboard</h1>
    <h3>Prices per toy type</h3>
    <chart-price
      v-if="isDataReady"
      :labels="labels"
      :avgs="avgs"
      class="chartPrice"
    ></chart-price>
  </div>
</template>

<script>
import chartPrice from "../cmps/chart-price.vue";

export default {
  components: {
    chartPrice,
  },
  data() {
    return {
      isDataReady: false,
    };
  },
  computed: {
    toys() {
      return this.$store.getters.toys;
    },
    labels() {
      return this.$store.getters.getToysLabels;
    },
    avgs() {
      const labelsMap = this.$store.getters.getLabelsMap;
      const avgs = [];
      for (const label in labelsMap) {
        let avg = labelsMap[label].sum / labelsMap[label].count;
        avgs.push(avg);
      }
      return avgs;
    },
  },
  watch: {
    toys: {
      immediate: true,
      handler() {
        if (this.toys.length) this.isDataReady = true;
        console.log(this.isDataReady);
      },
    },
  },
};
</script>

