<template>
  <section class="toy-filter main-layout">
    <div class="filter-section">
      <label class="label-input-section">
        Search a toy:
        <el-input
          @input="setFilter"
          placeholder="Search...."
          v-model="filterBy.name"
        >
        </el-input>
      </label>
      <label class="label-input-section">
        Stock:
        <el-select
          @change="setFilter"
          v-model="filterBy.inStock"
          placeholder="Select stock"
        >
          <el-option value="">All</el-option>
          <el-option value="true">In stock</el-option>
          <el-option value="false">Not in stock</el-option>
        </el-select>
      </label>
      <label class="label-input-section">
        Labels:
        <el-select
          v-model="filterBy.label"
          multiple
          @change="setFilter"
          placeholder="Select"
        >
          <el-option v-for="(item, idx) in labels" :key="idx" :value="item">
          </el-option>
        </el-select>
      </label>
      <label class="label-input-section">
        Sort By:
        <el-select @change="setFilter" v-model="filterBy.sort">
          <el-option value="time">Time</el-option>
          <el-option value="name">Name</el-option>
          <el-option value="price">Price</el-option>
        </el-select>
      </label>
      <!-- <div class="paging-section">
      <span v-for="page in pages" :key="page">
        <button @click="changePage(page - 1)">{{ page }}</button>
      </span>
    </div> -->
    </div>
  </section>
</template>

<script>
import { utilService } from "@/services/util-service.js";

export default {
  name: "toyFilter",
  props: {},
  data() {
    return {
      filterBy: {
        name: "",
        label: [],
        inStock: null,
        pageIdx: 0,
        sort: "time",
      },
    };
  },
  created() {
    this.setFilter = utilService.debounce(this.setFilter, 1000);
  },
  methods: {
    setFilter() {
      this.$emit("filtered", this.filterBy);
    },
    setSort() {
      this.$emit("sorted", this.sortBy);
    },
    changePage(pageNum) {
      this.filterBy.pageIdx = pageNum;
      this.setFilter();
    },
  },
  computed: {
    pages() {
      console.log(Math.ceil(this.$store.getters.getNumOfPages));
      return Math.ceil(this.$store.getters.getNumOfPages);
    },
    labels() {
      console.log(this.$store.getters.getToysLabels);
      return this.$store.getters.getToysLabels;
    },
  },
};
</script>


