<script>
import { Line } from "vue-chartjs";

export default {
  extends: Line,
  props: ["labels", "avgs"],
  mounted() {
    this.renderChart({
      labels: this.labels,
      datasets: [
        {
          label: "Average price",
          backgroundColor: "#f87979",
          data: this.avgs,
        },
      ],
    });
  },
};
</script>