<template>
  <section class="signup-section">
    <div class="img-container">
      <img :src="require(`@/assets/img/add-user.png`)" class="preview-img" />
    </div>
    <h1>welcome new user! Signup here</h1>
    <h2>
      Hello <span v-if="user">{{ getLoggedinUser.fullname }}</span>
    </h2>
    <form @submit.prevent="doSignup" class="signup-form">
      <label
        >Username:
        <input
          type="text"
          placeholder="Enter your username"
          v-model="username"
        />
      </label>
      <label
        >Fullname:
        <input
          type="text"
          placeholder="Enter your fullname"
          v-model="fullname"
        />
      </label>
      <label
        >Password:
        <input
          type="text"
          placeholder="Enter your password"
          v-model="password"
        />
      </label>

      <button class="btn-action btn-signup">save</button>
    </form>
  </section>
</template>

<script>
import { userService } from "../services/user-service.js";

export default {
  data() {
    return {
      username: "",
      password: "",
      fullname: "",
      user: null,
    };
  },
  methods: {
    async doSignup() {
      try {
        // const user = await userService.signup(
        //   this.username,
        //   this.password,
        //   this.fullname
        // );
        const user = await this.$store.dispatch({
          type: "signup",
          username: this.username,
          fullname: this.fullname,
          password: this.password,
        });
        this.user = user;
        this.$store.commit({ type: "setLoggedinUser", user });
        this.$router.push("/toy");
      } catch (err) {
        console.log(err);
      }
    },
  },
  computed: {
    getLoggedinUser() {
      console.log(this.$store.getters.loggedinUser);
      return this.$store.getters.loggedinUser;
    },
  },
};
</script>

