<template>
  <section class="login-section">
    <!-- <a @click="logout" to="/logout">Logout</a> -->
    <div class="img-container">
      <img :src="require(`@/assets/img/user.png`)" class="preview-img" />
    </div>
    <h1>Welcome back! Login here</h1>
    <h2>
      Hello: <span>{{ getLoggedinUser.fullname }}!</span>
    </h2>

    <form @submit.prevent="doLogin" class="login-form">
      <label
        >Username:
        <input
          type="text"
          placeholder="Enter your username"
          v-model="username"
        />
      </label>
      <label
        >Password:
        <input
          type="text"
          placeholder="Enter your password"
          v-model="password"
        />
      </label>

      <button class="btn-action btn-login">save</button>
    </form>
    <button @click="doLogout" class="btn-action btn-logout">Log out</button>
    <details>
      <summary>Admin Section</summary>
      <ul>
        <li v-for="user in users" :key="user._id">
          <pre>{{ user }}</pre>
          <button @click="removeUser(user._id)">x</button>
        </li>
      </ul>
    </details>
  </section>
</template>

<script>
import { userService } from "../services/user-service.js";

export default {
  data() {
    return {
      username: "",
      password: "",
      user: null,
    };
  },
  created() {
    this.loadUsers();
  },
  methods: {
    async doLogin() {
      try {
        // const user = await userService.login(this.username, this.password);
        const user = await this.$store.dispatch({
          type: "login",
          username: this.username,
          password: this.password,
        });
        this.user = user;
        this.$store.commit({ type: "setLoggedinUser", user });
        this.$router.push("/toy");
      } catch (err) {
        console.log(err);
      }
    },
    async doLogout() {
      try {
        this.$store.dispatch({ type: "logout" });
        // const ans = await userService.logout();
        this.user = null;
        this.$router.go();
      } catch (err) {
        console.log(err);
      }
    },
    loadUsers() {
      this.$store.dispatch({ type: "loadUsers" });
    },
    async removeUser(userId) {
      try {
        await this.$store.dispatch({ type: "removeUser", userId });
        console.log("User removed");
      } catch (err) {
        console.log("Failed to remove user");
      }
    },
  },
  computed: {
    getLoggedinUser() {
      return this.$store.getters.loggedinUser || "";
    },
    users() {
      return this.$store.getters.users;
    },
  },
};
</script>
