<template>
  <div class="list-section">
    <ul class="toy-list">
      <li v-for="toy in toys" :key="toy._id" class="toy-preview-container">
        <toy-preview :toy="toy" />
        <div class="btns-container">
          <!-- <div class="btns-container"> -->
          <button
            v-if="getIsLoggedinUserAdmin"
            class="btn-action"
            @click="remove(toy._id)"
          >
            Delete
          </button>
          <router-link class="router-link-btn" :to="'/toy/' + toy._id"
            >Details</router-link
          >
          <router-link
            v-if="getIsLoggedinUserAdmin"
            class="router-link-btn"
            :to="'/toy/edit/' + toy._id"
            >Edit</router-link
          >
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import toyPreview from "@/cmps/toy-preview.vue";

export default {
  name: "toyList",
  props: {
    toys: {
      type: Array,
      validator: (toys) =>
        toys.every((toy) => toy._id && toy.price && toy.name),
      default: [],
    },
  },
  methods: {
    remove(toyId) {
      this.$emit("remove", toyId);
    },
  },
  components: {
    toyPreview,
  },
  computed: {
    getIsLoggedinUserAdmin() {
      return this.$store.getters.getIsLoggedinUserAdmin;
    },
  },
};
</script>

