<template>
  <section class="toy-preview">
    <div class="toy-preview-info">
      <div class="img-container">
        <img
          :src="require(`@/assets/img/toy${randNum}.png`)"
          class="preview-img"
        />
      </div>
      <h2>{{ toy.name }}</h2>
      <h4>{{ toy._id }}</h4>
      <h4>{{ toy.price }}$</h4>
      <h4>In stock? {{ toy.inStock }}</h4>
    </div>
  </section>
</template>

<script>
export default {
  name: "toyPreview",
  props: {
    toy: {
      type: Object,
      default: {},
    },
  },
  computed: {
    randNum() {
      return Math.floor(Math.random() * 7 + 1);
    },
  },
};
</script>
