<template>
  <section v-if="toy" class="toy-details-section app-main">
    <h3>Toy details</h3>
    <div class="img-container">
      <img
        :src="require(`@/assets/img/toy${randNum}.png`)"
        class="preview-img"
      />
    </div>
    <h4>Name: {{ toy.name }}</h4>
    <h4>Id: {{ toy._id }}</h4>
    <h4>Price: {{ toy.price }}$</h4>
    <h4>
      Labels:
      <span v-for="(label, idx) in toy.labels" :key="idx">{{ label }}, </span>
    </h4>
    <h4>Created At: {{ toy.createdAt }}</h4>
    <h4>Is in stock? {{ toy.inStock }}</h4>
    <!-- <h4>
      Reviews:
      <span v-for="(review, idx) in reviews" :key="idx">{{ review }}</span>
    </h4>
    <form @submit.prevent="addReview">
      <h2>Add a review on this toy</h2>
      <input
        type="text"
        placeholder="Enter your review"
        v-model="reviewToEdit.txt"
      />
      <button>Add review</button>
    </form> -->
    <div class="reviews-container" v-if="reviews">
      <p>What do you think about this toy?..</p>
      <div class="review-card" v-for="(review, idx) in reviews" :key="idx">
        <div class="byUser">
          <p>{{ review.byUser.fullname }}</p>
        </div>
        <p>{{ review.txt }}</p>
        <span>{{ review.createdAt }}</span>
        <el-button @click="removeReview(review._id)" class="btn">X</el-button>
      </div>
      <form class="review-form">
        <input
          v-model="reviewToEdit.txt"
          placeholder="Your Opinion Matters..."
        />
        <el-button @click="addReview()">Save</el-button>
      </form>
    </div>
    <router-link class="router-link-btn" to="/toy">Back</router-link>
  </section>
</template>

<script>
import { toyService } from "../services/toy-service.js";

export default {
  name: "toyDetails",
  components: {},
  data() {
    return {
      reviewToEdit: {
        txt: "",
        aboutToyId: "",
      },
      toy: null,
    };
  },
  created() {
    this.loadToy();
  },
  computed: {
    randNum() {
      return Math.floor(Math.random() * 7 + 1);
    },
    reviews() {
      return this.$store.getters.reviews;
    },
    getToyId() {
      return this.toy._id;
    },
  },
  methods: {
    removeReview(reviewId) {
      this.$store.dispatch({
        type: "removeReview",
        reviewId,
      });
    },
    async addReview() {
      try {
        await this.$store.dispatch({
          type: "addReview",
          review: this.reviewToEdit,
        });
      } catch (err) {
        console.log(err);
      } finally {
        this.reviewToEdit.txt = "";
      }
    },
    async loadToy() {
      try {
        const id = this.$route.params.toyId;
        const toy = await toyService.getById(id);
        this.toy = toy;
        this.reviewToEdit.aboutToyId = this.toy._id;
      } catch (err) {
        console.log(err);
      } finally {
        this.$store.dispatch({ type: "loadReviews", toyId: this.toy._id });
      }
    },
  },
  watch: {
    "$route.params.toyId": {
      handler() {
        let toyId = this.$route.params.toyId;
        console.log("Changed to", toyId);
        this.$store.dispatch({ type: "setCurrToy", toyId });
      },
      immediate: true,
    },
  },
};
</script>

